.spinnerarea {
    width: 40px;
    height: 40px;
    position: absolute;
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    top: 50%;
    left: 50%;
    transform: translate(250%, 0);
}

.tableTd_title{
  font-size: 1rem;
}
.tableTd_name{
  font-size: 1.3rem;
  font-weight: 700;
}

.tableTh{
  font-weight: bold;
}
.tableTd, .tableTh, .tableTd_name, .tableTd_title{
  padding: .1rem .5rem;
}

.spinnerarea_deatails{
  z-index: 9999;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  align-items: center;
  min-height: 160px;
}

.not_found{
  color: rgb(124, 3, 3);
  text-align: center;
  font-weight: bold;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.not_selected{
  text-align: center;
  color: rgb(126, 1, 1);
  font-weight: bold;
  padding: 2rem;
}
.container{
    margin-top: 2rem;
}

@media (max-width: 768px) {
    .some-class {
      left: 50%;
    }
  }
  
  @media (max-width: 1200px) {
    .spinnerarea {
    transform: translate(-50%, 0);
    }
  }